.banner{
    display: flex;
    width: 100%;
}
.banner img{
    width: 100%;
    height: 350px;
}

@media only screen and (max-width: 768px) {
    .banner img{
        height: 200px;
    }
}

@media only screen and (max-width: 479px) {
    .banner img{
        height: 150px;
    }
}